import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export default class ListItem extends Component {
    render() {
        return (
            <li className={'list-item-card'}>
                <h3 className='list-item-card-title'>{this.props.name}</h3>
                <button onClick={this.props.clickHandler} className="btn btn-danger list-item-card-btn" type="button"><FontAwesomeIcon icon='times' /></button>
            </li>
        )
    }
}
